import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { Observable } from 'rxjs';

import 'rxjs/add/operator/toPromise';

import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';


@Component({
	selector: 'home',
	templateUrl: './home.component.html'
})
export class HomeComponent implements OnInit{
	user: Observable<firebase.User>;

	bookmarks: FirebaseListObservable<any[]>;

	email: string;
	password: string;

	uid: string;

	afx: AngularFireDatabase;

	currentUserEmail: string;
	errorMessage: string;

	news: Observable<any[]>;

	userChange = (user) => {
		if(user != null) {
			console.log(user.uid + 'changed');
			this.uid = user.uid;
			this.currentUserEmail = user.email;
			this.bookmarks = this.afx.list('/bookmarks/' + this.uid);
		}
	}

	showError = (error) => {
		this.errorMessage = error.message;
	}

	constructor(
		private afAuth: AngularFireAuth,
		private af: AngularFireDatabase,
		private http: Http) {
		this.user = this.afAuth.authState;
		this.afx = af;
	}

	ngOnInit(): void {
		this.afAuth.auth.onAuthStateChanged(this.userChange);
		this.loadData();
		// every 30 seconds
		Observable.interval(30000).subscribe(() => {
			console.log('schedule call');
			this.loadData();
		});
  	}

	register(){
		//console.log('registering ' + this.email + ' and ' + this.password);

		this.afAuth.auth.createUserWithEmailAndPassword(this.email, this.password)
			.catch(this.showError);
	}

	login(){
		this.afAuth.auth.signInWithEmailAndPassword(this.email, this.password)
			.catch(this.showError);
	}

	logout() {
		this.afAuth.auth.signOut();
		this.bookmarks = null;
	}

	bookmark(item){
		this.bookmarks.push(item);
		console.log(item);
		console.log('book marked');
	}

	loadId = (id) => {
		var url = 'https://hacker-news.firebaseio.com/v0/item/' + id + '.json';
		return this.http.get(url);
	}

	private loadData(){
		var url = 'https://hacker-news.firebaseio.com/v0/newstories.json';

		this.http.get(url)
			.subscribe((response) => {
				var ids = response.json() as string[];
				// only load last 5 items
				var obs = ids.slice(0, 5).map(this.loadId);
				this.news = Observable.forkJoin(obs).map((res) => res.map((r) => r.json()));
			});
	}

	private handleError(error: any): Promise<any> {
		console.error('An error occured', error);
		return Promise.reject(error.message || error);
	}
}


